.header {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    .logo-container {
        height: 100%;
        width: 60px;
        padding: 15px;
    }

    .logo {
        width: 100%;
        height: auto;
    }

    .title-container {
        width: 100%;
    }

    .options {
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-end;

        .option {
            padding: 10px 15px;
            cursor: pointer;

            &:hover {
                font-weight: 800;
                border-bottom: solid thin;
            }
        }
    }

    a {
        text-decoration: none;
    }
}